* {
  padding: 0;
  margin: 0;
}
body {
  font-family: 'Montserrat-Regular', sans-serif;
  font-size: 16px;
}
.lightTheme {
  background-color: white;
  color: black;
}
.darkTheme {
  background-color: #212121;
  color: #f5f5f5;
}
img {
  display: block;
  box-sizing: content-box;
}
a {
  text-decoration: none;
}
button {
  background-color: unset;
  border: none;
}
main {
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 1919px) {
  main {
    padding: 15px;
  }
}
@media (max-width: 1220px) {
  main {
    padding: 15px;
    width: 100%;
  }
}
@media (max-width: 767px) {
  body {
    font-size: 14px;
  }
  main {
    max-width: 737px;
    padding: 15px;
    width: 100%;
  }
}
